

button {
  background: #34495e;
  border: 0;
  color: #fff;
  display: block;
  width: 100%;
  
  border-radius: 4px;
  padding: 0;
  line-height: 40px;
}

.local {
  background: #fff;
  border-radius: 4px;
  width: 300px;
  margin: 20px auto;
  padding: 15px;
  position: absolute;
  top: 100%;
  left: -10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.local-container {
  width: 200px;
  margin: 50px auto;
  position: relative;
}

.heading {
  margin: 0;
  text-align: center;
  background-color: #004E98;
  padding: 0.5cm;
}

.map {
  margin: 0;
  width: 85%;
  height: 100vh;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

h1{
color: #C0C0C0;
}

.text-input {
  width: 15%;
  background-color: #3A6EA5;
  height: 100vh;
}

.text-input input {
  width: 100%;
  line-height: 0.8cm;
  padding: 17px;
  padding-left: 55px;
  width: 300px;
  border: 1px solid #f5f5f5;
  font-size: 13px;
  color: gray;
  background-image: url('https://s2.gifyu.com/images/search-blue.png');
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 5vh;
  outline: 0;
}

.text-input ul {
  list-style-type: none;
  padding-left: 0;
  text-align: center;
}

.text-input li {
  text-decoration: none;
  border-bottom: 2px solid white;
  color: white;
  line-height: 2cm;
}

.menu {
  display: none;
}

.error {
  color: red;
}


li:focus {
  background-color: #9f8ef9;
}
